.home {
  // min-height: calc(100vh - 54px);
  padding: 40px 0 ;

  &__container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    // gap: 30px;
    align-items: center;
  }

  &__left {
    max-width: 45%;
    padding: 10px;
    &-title {
      font-size: 30px;
      margin: 0;
    }
    &-subtitle {
      margin: 0 0 15px;
      font-size: 25px;
      font-weight: 600px;
    }
    &-links {
      margin: 0 0 25px;
      padding: 0;
      list-style: none;
      gap: 20px;
      display: flex;
    }
    &-icon {
      font-size: 2em;
      transition: 0.3s ease;
      fill: lighten($color: #038cfd, $amount: 25);
      &:hover {
        transform: scale(1.2);
        fill: #038eff;
        object-fit: cover;
      }
    }
    &-cta {
      display: inline-block;
      background-color: lighten($color: #038cfd, $amount: 20);
      padding: 10px 30px;
      transition: 0.3 ease;
      border-radius: 5px;
      color: white;
      &:hover {
        background-color: #038cfd;
        transition: 0.2s ease;
        transform: scale(1.1);
      }
    }
  }
  &__right {
    max-width: 35%;
    text-align: right;
    &-img {
      width: 100%;
      height: 400px;
      
      object-fit: cover;
      border-radius: 50%;
    }
  }
}
@media screen and (max-width: 768px) {
    .home{
        padding: 5vh 0;
        &__left,
        &__right{
            max-width: 100%;
            text-align: center;
        }
        &__right{
            order: -1;
            &-img{
                width: 300px;
                height: 300px;
            }
        }
        &__left-links{
            justify-content: center;
        }
    }
}
